import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import managerHome from '@/components/home/managerHome'
Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login 
    },
    {
      path:'/managerHome',
      name: 'managerHome',
      component: managerHome
    }
  ]
})



